import React from "react";
import "hui/item";

export default class HAItem extends React.Component {
    static propTypes = {
        className: React.PropTypes.string,
        value: React.PropTypes.string,
        disabled: React.PropTypes.oneOfType([React.PropTypes.bool, React.PropTypes.string])
    };

    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <ha-item class={this.props.className} {...this.props}>
                {this.props.children}
            </ha-item>
        );
        /* jshint ignore:end */
    }
}
